import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))
const BalanceSheet = React.lazy(() => import('./views/balance-sheet/BalanceSheet'))
const Members = React.lazy(() => import('./views/members/Members'))
const Social = React.lazy(() => import('./views/social/Social'))
const About = React.lazy(() => import('./views/about/About'))
const Profile = React.lazy(() => import('./views/profile/Profile'))

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', element: Dashboard },
  { path: '/balance-sheet', name: 'Balance Sheet', element: BalanceSheet },
  { path: '/members', name: 'Members', element: Members },
  { path: '/social', name: 'Social', element: Social },
  { path: '/about', name: 'About', element: About },
  { path: '/profile', name: 'Profile', element: Profile },
]

export default routes
