import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilLockLocked, cilPhone } from '@coreui/icons'
import logo from 'src/assets/images/Logo.png'
import { getAuth, RecaptchaVerifier, signInWithPhoneNumber } from 'firebase/auth'
import Swal from 'sweetalert2'
import { AppFooter } from 'src/components'

const Login = () => {
  
  const [mobileNo, setMobileNumber] = useState()
  const [firebaseResult, setFirebaseResult] = useState()
  const [verificationCode, setVerificationCode] = useState()
  const [isCodeSent, setIsCodeSent] = useState(false)
  const navigate = useNavigate()

  const onSubmit = () => {
    var phoneno = /^\d{10}$/
    
    if(!(mobileNo.match(phoneno))) {
      Swal.fire({
        title: "Invalid Mobile Number",
        text: "Please enter correct mobile number!",
        icon: "warning",
        confirmButtonColor: "#006f95"          
      })
      return
    }
    
    const appVerifier = window.recaptchaVerifier;
    signInWithPhoneNumber(getAuth(), "+91"+mobileNo, appVerifier)
      .then((confirmationResult) => {
          setFirebaseResult(confirmationResult)
          setIsCodeSent(true)
          window.confirmationResult = confirmationResult;
      }).catch((error) => {
          Swal.fire('Error','Failed to send OTP try again!','error')
    });
  }

  const verifyCode = () => {
    if (firebaseResult.confirm) {
      firebaseResult.confirm(verificationCode).then(rs => {
        localStorage.setItem("user", JSON.stringify(rs.user))
        navigate("/dashboard")
      }).catch(rs => {
        Swal.fire({
          title: "Warning",
          text: "Please enter correct code",
          icon: "warning",
          confirmButtonColor: "#006f95"          
        })
      })
    }
  }

  const initRecaptcha= () => {
    window.recaptchaVerifier = new RecaptchaVerifier('sign-in-button', {
      'size': 'invisible'
    }, getAuth());
  }
  
  useEffect(() => {
    if (localStorage.getItem("user")) {
      navigate("/dashboard")
    } else {
      initRecaptcha()
    }
  }, [])

  return (
    <div className="bg-primary-25 min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={6}>
            <CCardGroup>
              <CCard>
                <CCardBody className='p-0 border border-5 border-primary'>
                  <h2 className="text-center bg-primary text-white border border-4" style={{"height":"40px"}}>Login</h2>
                  <CForm>
                    <img className="sidebar-brand-full p-3" src={logo} height="150px" width="100%" />
                    <div className='px-5 pb-5'>
                      <CInputGroup className="my-3 mt-4">
                        <CInputGroupText>
                          <CIcon icon={cilPhone} />
                        </CInputGroupText>
                        <CFormInput defaultValue={mobileNo} onChange={e => setMobileNumber(e.target.value)} placeholder="Mobile Number" autoComplete="username" />
                      </CInputGroup>
                      {isCodeSent && <CInputGroup className="my-3 mt-4">
                        <CFormInput className="text-center" type="number" defaultValue={verificationCode} onChange={e => { e.target.value.length <= 6 ? setVerificationCode(e.target.value) : e.target.value = e.target.value.substring(0,6); setVerificationCode(e.target.value.substring(0,6))}} placeholder="Code" autoComplete="username" />
                      </CInputGroup>}
                      <CRow>
                        <CCol xs={12} className="d-flex justify-content-center">
                          {!isCodeSent && <CButton onClick={onSubmit} color="primary" className="px-4" id="sign-in-button">
                            Send OTP
                          </CButton>}
                          {isCodeSent && <CButton onClick={verifyCode} color="primary" className="px-4">
                            Verify
                          </CButton>}
                        </CCol>
                      </CRow>
                    </div>
                  </CForm>
                  <AppFooter />
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
